.header-section {
    width: 70%;
    display: flex;
    padding: 15px 0;
    margin: 0 auto;
}

.button-pick {
    width: 15px;
    height: 15px;
    box-sizing: border-box;

    span {
        float: left;
        box-sizing: border-box;

        width: 45%;
        height: 45%;

        border: 1px solid white;
        background-color: transparent;

        @media  screen and (max-width: 768px) {
            border-color: black;
        }
    }
}

.header-btn, .header-btn-first {
    padding: 10px 54px;
    background-color: #ffc85e;
}

.square_1 {
    margin: 0 1px 1px 0;
    border-radius: 2px 1px 1px 1px;
}

.square_2 {
    border-radius: 1px 2px 1px 1px;
    margin: 0 0 1px 0;
}

.square_3 {
    border-radius: 1px 1px 1px 2px;
}

.square_4 {
    border-radius: 1px 1px 2px 1px;
    margin: 0 0 0 1px;
}

.notification {
    position: absolute;
    top: -5px;
    right: -5px;

    border: 2px solid white;

    background-color: #ffc85e;
    font-size: 12px;
    padding: 1px 4px;
    border-radius: 50%;
}

.search-input {
    padding: 11px 10px 11px 20px;
    margin: auto 0;
}



@media screen and (max-width: 768px) {
    .search-input,
    .phone-numbers,
    .header-btn,
    .summary,
    .not-needed {
        display: none;
    }
    .header-btn-first{
        flex-direction: row-reverse;
        background-color: transparent;
        color: black !important;
    }
}
